.product-plus {
  padding-top: 14px;
  padding-bottom: 17px;

  border-top: $border;

  &:last-child {
    border-bottom: $border;
  }
}

.product-plus__title {
  margin: 0 0 15px;

  @include font(16px, 16px, $font-regular, $black);
  text-transform: uppercase;
}

.product-plus__list {
  @include list-reset;
}

.product-plus__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 14px;
}

.product-plus__info {
  @include font(14px, 14px);
}

.product-plus__title--more {
  margin: 0;

  @include font(16px, 20px, $font-regular, $black-two);
}

.product-plus__info--more {
  @include font(12px, 16px);
}

@media (min-width: $tablet) {
  .product-plus {
    padding: 28px 38px;

    border: $border;
  }

  .product-plus__wrapper {
    display: flex;
    justify-content: space-between;
  }

  .product-plus__title {
    @include font(20px, 20px, $font-regular, $black);
  }

  .product-plus__info {
    @include font(16px, 20px);
  }
}

@media (min-width: $desktop) {
  .product-plus {
    display: flex;
    padding: 28px 0;

    border: none;
    border-top: $border;
  }

  .product-plus__wrapper {
    width: 100%;
  }

  .product-plus__title {
    margin: 0;
  }

  .product-plus__button {
    width: 245px;
  }

  .product-plus__box {
    width: 895px;
  }
}
